import React, { useEffect, useState } from "react";
import MainRouter from "components/Router";
import { authService } from "fbase";
import PropTypes from 'prop-types';
import GlobalStyle from 'components/GlobalStyle';

function App() {
  // Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Î°úÍ∑∏Ïù∏ ÏïàÎêú ÏÉÅÌÉú
  const [init, setInit] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userObj, setUserObj] = useState(null);
  //console.log(isLoggedIn); // null Ïù∏ Ïù¥Îâ¥Îäî firebaseÍ∞Ä Ï¥àÍ∏∞ÌôîÎêòÍ≥† Î™®Îì†Í±∏ Î°úÎìúÌï†ÎñÑÍπåÏßÄ Í∏∞Îã§Î†§Ï§Ñ ÏãúÍ∞ÑÏù¥ ÏóÜÍ∏∞ÎïåÎ¨∏

  //  onAuthStateChangedÎäî stateÏùò Î≥ÄÌôîÎ•º Îì£Í≥† ÏûàÎã§ 
  useEffect(()=>{
    authService.onAuthStateChanged((user)=>{
      if(user) {
        setIsLoggedIn(true); 
        // setUserObj(user)
        setUserObj({
          displayName: user.displayName,
          uid: user.uid,
          updateProfile : (args)=> user.updateProfile(args) 
        })
      } else {
        setIsLoggedIn(false)
      }
      setInit(true);
    })
  },[])

  const refreshUser = () =>{
    const user = authService.currentUser;
    setUserObj({
        displayName: user.displayName,
        uid: user.uid,
        updateProfile : (args)=> user.updateProfile(args) 
    });
  }

  return (
    <>
      <GlobalStyle />
      {init ? <MainRouter isLoggedIn={isLoggedIn} userObj={userObj} refreshUser={refreshUser} /> : "Initializing..." }
    </>
  );
}

export default App;


App.propTypes = {
  init : PropTypes.bool,
  isLoggedIn : PropTypes.bool,
  userObj: PropTypes.object
} 